<template>
  <div id="edit-gig" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>Edit Gig</h1>
      <form @submit.prevent="updategig">
        <div class="card mt-4 mb-4">
          <div class="card-header">
            <h4>State</h4>
          </div>
          <div class="form-group pt-4 pl-4">
            <input 
               type="radio" 
               id="publicState"
               value="public"
               v-model="state"
               >
            <label class="ml-1" for="publicState">Public</label>
            &nbsp;
            <input 
               type="radio" 
               id="draftState"
               value="draft"
               v-model="state"
               >
            <label class="ml-1" for="draftState">Draft</label>
          </div>
        </div>
        <label> Act </label>
        <vue-suggest
          v-model="act_name"
          :list="acts"
          :filter-by-query="true"
          value-attribute="id"
          display-attribute="name"
          @select="selectAct"
          >
        </vue-suggest>
        <input
          type="hidden"
          name="act_id"
          v-model="act_id"
          >
        </input>
        <label> Venue </label>
        <vue-suggest
          v-model="venue_name"
          :list="venues"
          :filter-by-query="true"
          value-attribute="id"
          display-attribute="name"
          @select="selectVenue"
          >
        </vue-suggest>
        <input
          type="hidden"
          name="venue_id"
          v-model="venue_id"
          >
        </input>
        <label> Event </label>
        <vue-suggest
          v-model="event_name"
          :list="events"
          :filter-by-query="true"
          value-attribute="id"
          display-attribute="name"
          @select="selectEvent"
          >
        </vue-suggest>
        <input
          type="hidden"
          name="event_id"
          v-model="event_id"
          >
        </input>
        <label>Gig Length</label>
        <input
          type="text"
          class="gigLength"
          name="gigLength"
          v-model="gigLength"
          @select="calcFinish"
          >
        </input>
        <label> Start </label>
        <vue-timepicker
          name="startTime"
          v-model="startTime"
          format="HH:mm"
          :minute-interval="5"
          @change="calcFinish"
          >
        </vue-timepicker>
        <p>Finish time {{ finishTime.toLocaleString() }}</p>
        <div class="mt-3">
          <button id="submit" class="btn btn-lg btn-block btn-primary pr-sm">Submit</button>
          <router-link to="/" class="btn btn-lg btn-block btn-secondary">Cancel</router-link>
        </div>
       
      </form>
    </div>
  </div>
</template>
<script>

  import firebase from 'firebase/app'
  import db from '../lib/firebaseInit'
  import VueSuggest from 'vue-simple-suggest'
  import 'vue-simple-suggest/dist/styles.css'
  import VueTimepicker from 'vue2-timepicker'
  export default {
    name:'edit-gig'
    , components: {
      VueSuggest
      , VueTimepicker
    } 
    , data() {
      return {
        act_name: ''
        , act_id: ''
        , venue_name: ''
        , venue_id: ''
        , event_name: ''
        , event_id: ''
        , event_date: ''
        , startTime: {
          HH:"10"
          , mm:"00"
        }
        , finishTime: '' 
        , acts: []
        , venues: []
        , events: []
        , startTimeStamp: 0
        , timestamp: new Date().getTime()
        , timeChange: false
        , gigLength: 0
        , state: ''
      }
    },
    created() {
      db.collection('gigs')
      .doc( this.$route.params.gig_id )
      .onSnapshot( doc => {
        const data = doc.data()
        const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
        let updatedLocal = ''
        if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
        this.gig_id = doc.id
        this.act_name = data.act_name
        this.act_id = data.act_id
        this.venue_name = data.venue_name
        this.venue_id = data.venue_id
        this.event_name = data.event_name
        this.event_id = data.event_id
        this.gigLength = data.gigLength*1
        this.startTime = {
          HH: data.startTime.getHours()
          , mm: data.startTime.getMinutes()
        }
        this.finishTime = data.finishTime
        this.state = data.state
        this.timestamp = data.startTime.setHours(0,0,0,0)
      } )
      db.collection('acts')
      .orderBy('act_name')
      .get()
      .then(qs => {
        qs.forEach( doc => {
          const data = doc.data()
          const act = {
            'id':doc.id
            , 'name': data.act_name
          }
          if( data.state == 'public' )
            this.acts.push( act )
        } )
      })
      db.collection('venues')
      .orderBy('name')
      .get()
      .then(qs => { 
        qs.forEach( doc => { 
          const data = doc.data()
          const venue = {
            'id':doc.id
            , 'name': data.name
          }
          if( data.state == 'public' )
            this.venues.push( venue )
        } ) 
      } )
      db.collection('events')
      .orderBy('name')
      .get()
      .then(qs => { 
        qs.forEach( doc => { 
          const data = doc.data()
          const event = {
            'id':doc.id
            , 'name': data.name
            , 'date': data.date
            , 'gigLength': data.gigLength
          }
          if( data.state == 'public' )
            this.events.push( event )
        } ) 
      } )
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      onFileChange ( e ) {
        const files = e.target.files
        for ( var i=0;i<files.length;i++ ) {
          const fileReader = new FileReader()
          let filename = files[i].name
          if ( filename.lastIndexOf('.')  <=0 ) return alert( "please use a valid file" )
          fileReader.addEventListener( 'load', () => {
            this.imageUrls.push(fileReader.result) 
          } )
          fileReader.readAsDataURL( files[i] )
          this.images.push( files[i] )
        }
      },
      onPickFile () {
        this.$refs.fileInput.click()
      }, 
      fetchData() {
        db.collection('gigs')
        .doc( this.$route.params.gig_id )
        .onSnapshot( doc => {
          const data = doc.data()
          const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
          let updatedLocal = ''
          if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 

          this.gig_id = doc.id
          this.act_name = data.act_name
          this.act_id = data.act_id
          this.venue_name = data.venue_name
          this.venue_id = data.venue_id
          this.event_name = data.event_name
          this.event_id = data.event_id
          this.gigLength = data.gigLength
          this.startTime = {
            HH: data.startTime.getHours()
            , mm: data.startTime.getMinutes()
          }
          this.finishTime = data.finishTime
          this.state = data.state
        } )
      },
      selectAct (suggest) {
        this.act_id = suggest.id
        this.act_name = suggest.name
      },
      selectVenue (suggest) {
        this.venue_id = suggest.id
        this.venue_name = suggest.name
      },
      selectEvent (suggest) {
        this.event_id = suggest.id
        this.event_name = suggest.name
        this.event_date = suggest.date
        this.gigLength = suggest.gigLength
        this.timestamp = this.event_date.getTime()
        this.calcFinish()
      },
      calcFinish () {
        let gigLengthStamp = parseInt( this.gigLength * 60 * 1000 )
        this.startTimeStamp = parseInt( this.startTime.HH * 60 * 60 * 1000 ) + ( this.startTime.mm * 60 * 1000 )
        this.finishTime = new Date( parseInt( this.timestamp + this.startTimeStamp + gigLengthStamp ) )
        this.timeChange = true
      }, 
      updategig() {
        let updatedAt = new Date()
          , gig_id = this.$route.params.gig_id
          , ref = db.collection('gigs').doc( gig_id )

        this.$validator.validate()
        .then( result => {
          if(!result) {
            return alert('Please fill in the form as required')
          }
          let startTime = new Date( this.timestamp + this.startTimeStamp )

          ref.update( {
            act_name: this.act_name
            , act_id: this.act_id
            , venue_name: this.venue_name
            , venue_id: this.venue_id
            , event_name: this.event_name
            , event_id: this.event_id
            , gigLength: this.gigLength
            , startTime: startTime
            , finishTime: this.finishTime
            , state: this.state
            , updatedAt: updatedAt
            , year: '2020'
          } )
          .then( () => {
            this.$router.push( { 
              name: 'view-gig'
              , params: gig_id 
            } )
          } )
          .catch( error => alert( error ) )
        } )
      }
    }
  }
</script>

<style scoped>
.gigLength,
.time-picker {
  display: block;
  width: 100%;
  padding: 10px;
  border: 1px solid #cde;
  border-radius: 3px;
  color: black;
  background: white;
  outline: none;
}
.display-time {
  border:none !important;
}
</style>
