<template>
  <div id="view-gig" class="row justify-content-md-center mt-5">
    <div class="col-sm-6">
      <h1>Gig Details</h1>
      <div class="mb-3">
        <router-link 
          to="/gigs/list" 
          class="btn btn-secondary ml-1">
          Back to gigs
        </router-link>
        <router-link 
          to="/gigs/new" 
          class="btn btn-secondary ml-1">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="card pt-0">
        <h3 class="card-header">
          {{ act_name }} at the 
          {{ venue_name }}
        </h3>
        <div v-if="state" class="card-text p-3">
          <div class="badge badge-primary mr-1 p-2">
            <i class="fas fa-calendar-check"></i>
            {{ event_name }}
          </div>
          <div class="badge badge-secondary mr-1 p-2">
            <i class="fas fa-eye"></i>
            {{ state }}
          </div>
        </div>
        <div class="card-body">
          <h5 class="first">
            From: {{ startTime }}
          </h5>
          <div class="card-text">
            To: {{ finishTime }}
          </div>
        </div>
        <div class="card-footer">
          <div v-if="createdAt != 'Invalid Date'" class="card-muted">Created: {{ createdAt }}</div>
          <div v-if="updatedAt != ''" class="card-muted">Last updated: {{ updatedAt }}</div>
        </div>
      </div>
      <div class="mt-3">
        <router-link v-bind:to="{ name:'edit-gig', params: gig_id }" class="btn btn-lg btn-block btn-outline-secondary">Edit</router-link>
        <button v-on:click="deletegig" class="btn btn-lg btn-block btn-danger">Delete</button>
      </div>
  </div>
</div>
</template>

<script>
import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'view-gig'
  , data() {
    return {
      gig_id: ''
      , act_name: ''
      , venue_name: ''
      , event_name: ''
      , startTime: ''
      , finishTime: ''
      , state : ''
      , createdAt: ''
      , updatedAt: ''
    }
  },
  created() {
    db.collection('gigs')
    .doc( this.$route.params.gig_id )
    .onSnapshot( doc => {
      const data = doc.data()
      const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
      let updatedLocal = ''
      if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
      this.gig_id = doc.id
      this.act_name = data.act_name
      this.venue_name = data.venue_name
      this.event_name = data.event_name
      this.startTime = data.startTime.toLocaleString()
      this.finishTime = data.finishTime.toLocaleString()
      this.state = data.state
      this.createdAt = createdLocal
      this.updatedAt = updatedLocal
    } )
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('gigs')
      .doc( this.$route.params.gig_id )
      .onSnapshot( doc => {
        const data = doc.data()
        const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
        let updatedLocal = ''
        if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
        this.gig_id = doc.id
        this.act_name = data.act_name
        this.venue_name = data.venue_name
        this.event_name = data.event_name
        this.startTime = data.startTime.toLocaleString()
        this.finishTime = data.finishTime.toLocaleString()
        this.state = data.state
        this.createdAt = createdLocal
        this.updatedAt = updatedLocal
      } )
    },
    deletegig() {
      if( confirm( 'Are you sure' ) ) {
        db.collection('gigs')
        .doc( this.$route.params.gig_id )
        .onSnapshot( doc => {
          doc.ref.delete()
          this.$router.push('/gigs/list')
        } )
      }
    }
  }
}
</script>
<style scoped>
h5 {
  margin-top:2rem;
  padding-top:1rem;
  border-top: thin #eee solid;
}
.first {
  margin-top:0;
  padding-top:0;
  border-top:none
}
.list-group {
  margin-bottom:2rem
}
</style>
