<template>
  <div id="list-gigs" class="row justify-content-md-center mt-5 mb-5">
    <div class="col-sm-6">
      <h1>List {{ year }} gigs </h1>
      <div class="mb-3">
        <router-link 
          to="/gigs/new" 
          class="btn btn-secondary ml-1">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="list-group">
        <router-link 
          v-for="gig in gigs" 
          v-bind:key="gig.id" 
          v-bind:to="gig.id" 
          class="list-group-item list-group-item-action"
          >
          {{ gig.act_name }}
          &nbsp;
          {{ gig.venue_name }}
          &nbsp;
          {{ gig.event_name }}
          &nbsp;
          {{ gig.startTime }}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import db from '../lib/firebaseInit'
export default {
  name:'list-gigs'
  , data() {
    const year = this.$route.query.year
    return {
      updatedAt:''
      , createdAt: ''
      , gigs: []
      , act_name: ''
      , venue_name: ''
      , event_name: ''
      , startTime: ''
      , year: year
    }
  }, 
  created() {
    const year = this.$route.query.year
    var query = db.collection('gigs')
    if( year ) query = query.where( 'year', '==', year )
    query.orderBy('startTime')
    .get()
    .then(qs => { 
      qs.forEach( doc => { 
        const data = doc.data()
        const gig = {
          'id':doc.id
          , 'act_name':data.act_name
          , 'venue_name':data.venue_name
          , 'event_name':data.event_name
          , 'startTime': data.startTime.toLocaleString()
          , 'createdAt': data.createdAt
          , 'updatedAt': data.updatedAt
        }
        this.gigs.push( gig )
      } ) 
    } )
  }
}
</script>
